import { lazy } from 'react';

const Home = lazy(() => import('../views/Home'));
const Product = lazy(() => import('../views/Products'));
const Research = lazy(() => import('../views/Research'));
const Blog = lazy(() => import('../views/Blog/'));
const ArticleDetail = lazy(() => import('../views/Research/ArticleDetail'));
const NewsDetail = lazy(() => import('../views/Blog/NewsDetail'));
const Rank = lazy(() => import('../views/Blog/Rank'));
const Demo = lazy(() => import('../views/Demo'));
const DemoCompD = lazy(() => import('../views/Demo/CompD'));


const routes = [
  {
    id: 'home',
    path: '/',
    component: Home,
  },
  {
    id: 'product',
    path: '/product',
    component: Product,
  },
  {
    id: 'research',
    path: '/research',
    component: Research,
  },
  {
    id: 'blog',
    path: '/blog',
    component: Blog,
  },
  {
    id: 'articleDetail',
    path: '/articleDetail/:id',
    component: ArticleDetail,
  },
  {
    id: 'newsDetail',
    path: '/newsDetail/:id',
    component: NewsDetail,
  },
  {
    id: 'rank',
    path: '/rank',
    component: Rank,
  },
  {
    id: 'demo',
    path: '/demo',
    component: Demo,
  },
  {
    id: 'democompd',
    path: '/demo/compd',
    component: DemoCompD,
  },
];

export default routes;